.LiveScores {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: flex-start;
    gap: 50px;
    min-height: calc(100vh - 50px);
    padding-top: 100px;
    box-sizing: border-box;
    padding-bottom: 20px;



}

.TennisLiveScoreTable {
    width: 400px;
    height: 200px;
    box-sizing: border-box;
    margin: 0;

    /* font-size: 20px; */
}

.TennisLiveScoreTable:hover {
    background-color: rgba(241, 255, 189, 0.305);

}

