.TennisLiveScoreTable .Doubles {
    background-color: aliceblue;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    gap: 5px;
    height: 100%;
    box-sizing: border-box;
    width: 100%;


}

.TennisLiveScoreTable .Singles {
    display: flex;
    justify-content: flex-start;
    align-content: center;
    gap: 5px;
    height: 100%;
    box-sizing: border-box;

}

.x {
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.y {
    /* width: 50px; */
}



.TennisLiveScoreTable td:nth-child(2) {
    background-color: rgb(222, 222, 222);
}

.TennisLiveScoreTable td {
    min-width: 50px;
    min-height: 70px;
}

/* .TennisLiveScoreTable  */


@media screen and (max-width: 600px) {
    .LiveScores{
        /* flex-direction: column; */
        justify-content: center;
        align-items: flex-start;
        gap: 20px;
    }


    .TennisLiveScoreTable{
        width: 80%;
        height: 150px;
        font-size: 80%;
        /* margin: 0; */
        margin-top: 0;
        margin-bottom: 0;

    }

    .LiveScores img {
        width: 10px;
    }

    .LiveScores .Spinner {
        width: 150px;
    }

    .TennisLiveScoreTable td {
        width: max-content;
        min-width: 30px;
        /* min-height: 70px; */
    }

    .LiveScores th{
        height: auto;
    }

    .LiveScores tr {
        /* height: 20px; */
    }
