.PopUp {

    height: 400px;
    /* width: 300px; */
    background-color: aliceblue;
    border: 5px solid black;
    /* position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%); */
    z-index: 10;
    position:fixed;
    top:50%;
    left:50%;
    -ms-transform: translateX(-50%) translateY(-50%);
    -webkit-transform: translate(-50%,-50%);
    transform: translate(-50%,-50%);

}

.Overlay {
    /* position: absolute;
    top: 0;
    left: 0; */
    height: 100vh;
    background-color: rgba(119, 136, 153, 0.585);
    z-index: 1;
    width: 100%;

    position:fixed;
    top:50%;
    left:50%;
    -ms-transform: translateX(-50%) translateY(-50%);
    -webkit-transform: translate(-50%,-50%);
    transform: translate(-50%,-50%);

    /* display: block; */
}

